.chapter-top-hero {
    height: 100vh;
    height: 100vh;
    /* height: 100dvh; */
    position: relative;
    /* background-color: #00629B; */
    /* background-blend-mode: multiply; */
    animation: fadeIn 0.25s ease-in-out forwards;

}
@keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @media (prefers-reduced-motion: reduce) {
    /* CSS to disable motion goes here */
    .chapter-top-hero  {
      animation: none;
    }
  }

