.chapter-hero {
    min-height: 80vh;
}
.chapter-padding {
    padding-inline: max(5%, 0.7rem);
    /* margin-bottom: 4rem; */
}
.chapter-details {
    margin: 6rem auto 0 auto;
    font-size: clamp(1rem, 0.8em + 0.43vw, 1.7rem);
    font-weight: 550;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    column-gap: 3rem;
    row-gap: 3rem;
}
.chapter-details-section {
    width: 60%;
}
.chapter-details-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    /* margin-bottom:4px; */
}
.chapter-social-media a {
    text-decoration-line: none;
    margin-right: 10px;
    color: #222;
}
.chapter-social-media i {
    font-size: 1.7rem;
    color: #222;
}
.chapter-social-media svg {
    font-size: 1.7rem;
    width: 1.7rem;
    margin: 0;
    padding: 0;
    transition: 150ms ease-in;
}
  
.chapter-social-media i:hover {
    color: var(--link-color);
    transition: 150ms ease-in;
}
  
.chapter-social-media a:hover {    
      color: var(--link-color);
      transition: 150ms ease-in;
}

.chapter-join-link a {
    text-decoration: none;
    background-color: var(--ieee-blue);
    color: #fff;
    padding: 0.1rem 0.3rem;
    border-radius: 4px;
    border: 1px solid #eee;
    transition: 250ms ease-in;
}
.chapter-join-link a:hover {
    text-decoration: none;
    color: var(--ieee-blue);
    background-color: #fff;
}

.chapter-details .chapter-details-text {
    /* width: 60%; */
    /* line-height: calc(1ex / 0.32); */
    background-color: #ededed;
    color: #00629B;
    font-weight: 700;
    /* line-height: 1.7rem; */
    padding: 2rem;
    border: 2px solid #353535;
    overflow: visible;
    overflow-wrap: break-word;
    box-sizing: border-box;
    border-radius: 4px;
    filter: drop-shadow(2.5px 2.5px 2.5px rgba(8, 9, 13, 0.4));
}

.chapter-details img {
    width: max(20%, 250px);
    margin: 0 auto;
    filter: drop-shadow(2.5px 2.5px 2.5px rgba(8, 9, 13, 0.4));

}

.chapter-subgroups {
    width: inherit;
    --mask:
          radial-gradient(65.74px at 50% 88.50px,#000 99%,#0000 101%) calc(50% - 60px) 0/120px 51% repeat-x,
          radial-gradient(65.74px at 50% -58.5px,#0000 99%,#000 101%) 50% 30px/120px calc(51% - 30px) repeat-x,
          radial-gradient(65.74px at 50% calc(100% - 88.50px),#000 99%,#0000 101%) calc(50% - 60px) 100%/120px 51% repeat-x,
          radial-gradient(65.74px at 50% calc(100% + 58.50px),#0000 99%,#000 101%) 50% calc(100% - 30px)/120px calc(51% - 30px) repeat-x;
        -webkit-mask: var(--mask);
        mask: var(--mask);
        background-color: 		#00629B;
        padding-inline: max(3%, 1rem);
        padding-block: min(20%,5rem);
}
.chapter-subgroups h1 {
    color: #fff;
}
.chapter-subroups-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}
.chapter-subroups-wrapper {
    width: 100%;
    margin-top: 2rem;
    row-gap: 2rem;
}
.chapter-subgroup {
    overflow: hidden;
    width: 15%;
    min-width: 300px;    
    background-color: #fff;
    border-radius: 4px;
    margin: 0 auto;
    filter: drop-shadow(2.5px 2.5px 2.5px rgba(8, 9, 13,.4));

}
.chapter-subgroup img {
    /* width: 100%; */
    /* min-height: 200px; */
    border-bottom: 1px solid #ccc;
    height: 170px;
    object-fit: cover;
    width: auto;
    /* max-height: 160px; */
}
.chapter-subgroup .subgroup-title {
    margin: 0 auto;
    text-align: center;
    padding: 0.5rem 0 0 0;
}
.chapter-subgroup p {
    padding: 0.5rem 0.5rem 0.1rem 0.5rem;
    line-height: 1.2rem;
    display: -webkit-box;
    -webkit-line-clamp: 11;
    -webkit-box-orient:vertical;
    overflow: hidden;
}
.chapter-subroups-wrapper img {
    width: 100%;
}
.chapter-section {
    /* margin-top:min(5rem,350px); */
}

.chapter-events {
    margin-top:5rem;
    position: relative;
}
.chapter-section-title {
    font-size: clamp(2rem, 2.5vw, 6rem);
    color:#00629B;
    font-weight: 700;
}

.chapter-events-wrapper {
    margin-top:3rem;
}

.chapter-people {
    margin-top: 5rem;
}
.chapter-people .people-row {
    margin-top: 3rem;
}

@media screen and (max-width:1100px) {
    .chapter-details {
        column-gap:1.5rem;
    }
    .chapter-details .chapter-details-section{
        width: 65%;
    }
}

@media screen and (max-width:870px) {
    .chapter-details {
        flex-direction: column;
    }
    .chapter-details .chapter-details-section {
        width: 100%;
    }
    .chapter-details .chapter-details-text {
        padding: 1rem;
    }
}
@media screen and (max-width:550px) {
    .chapter-page {
        padding-top:5rem;
    }
}
