:root {
  --icon-dimensions: 4rem;
  --full-w: 100%;
  --full-h: 100%;
  --default-font: -apple-system, BlinkMacSystemFont, Roboto, helvetica neue,
    sans-serif;
  --spartan: "LeagueSpartanVariable";
  /* --normal-font: "Inter Tight"; */
  /* --normal-font: 'Inter'; */
  /* --normal-font: 'Noto Sans'; */
  /* --normal-font:'LeagueSpartanVariable'; */
  --normal-font: 'Source Sans Pro';
  /* --h1-clamp: clamp(6.5rem, 15vw, 30rem); */
  /* --normal-font: 'Inter Tight'; */
  --h1-clamp: clamp(4.3rem, 15vw, 18vw) ;
  --h2-clamp: clamp(0.5rem, 4.4vw, 25rem);
  --default-font-size: 1.2rem;
  --duration: 350ms;
  --gap: 0.4rem;
  --default-padding: 1.2rem;
  --z-20: 20;
  --z-30: 30;
  --black: rgb(12, 12, 10);
  --white: rgb(255, 255, 255);
  --pale-blue: #c1dcee;
  --blue: #389dd6;
  --dark-blue: #064f99;
  --ieee-blue: #00629B;
  --link-color: #0063D7;
  --light-blue: #68b0de;
  --bg-1: url("../src/assets/images/landing/background_hero_optimized.jpg");
  /* --bg-2: url("../src/assets/images/landing/greekflag3-1.webp"); */
  --bg-2: url("data:image/webp;base64,")
}

  @font-face {
    font-family: "LeagueSpartanVariable";
    /* src: url("https://assets.codepen.io/1149983/LeagueSpartan-VF.woff2") format("woff2-variations"); */
    src: url(../src/assets/LeagueSpartan-VF.woff2) format("woff2-variations");
    font-weight: 200 900;
  }
  
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /* font-family: var(--spartan), var(--default-font); */
  
  }
  html, body {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    width: 100%;
    font-family: var(--normal-font);
    /* height: 100%; */
    /* font-size: 62.5%; */
    /* background-color: var(--black); */
    /* font-family: var(--spartan), var(--default-font); */
    /* height: -webkit-fill-available; */
    text-rendering: optimizelegibility;
    /* text-transform: uppercase; */
    font-weight: 650;
  }
  
  ::selection {
    -webkit-background-clip: text;
    -webkit-box-direction: clone;
    -webkit-text-fill-color: var(--yellow);
    background-color: var(--light-blue);
    color: var(--dark-blue);
  }
  .page-container {
    margin-top: 12rem;
    /* padding: 0 7rem; */
    padding-inline: max(5%, 0.7rem);
  }
  h1.cool-title {
    background-color: var(--ieee-blue);
    /* background-color: 	#981D97; */
    /* background: linear-gradient(to right, hotpink, #00629B); */
    width: fit-content;
    color: #fff;
    padding: 1rem;
    border-radius: 8px;
    font-size: clamp(2rem, 3vw, 7rem);
    font-weight: 750;
    text-transform: uppercase;
    line-height: calc(1ex / 0.42);
  }
  pre {
    /* overflow-x: auto !important; */
    white-space: pre-wrap !important;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap !important;  /* Mozilla, since 1999 */
    white-space: -pre-wrap !important;      /* Opera 4-6 */
    white-space: -o-pre-wrap !important;    /* Opera 7 */
    word-wrap: break-word !important;       /* Internet Explorer 5.5+ */
    font-family: var(--normal-font);
}
  mark {
    background-color: var(--ieee-blue);
    color: #fff;
    border-radius: 4px;
    padding:1px 5px;
  }

.title-with-hr {
  position: relative;
  width: fit-content;
}
.title-with-hr:before {
  content: "";
  width: 50%;
  position: absolute;
  bottom:-7px;
  /* background-color: #981D97; */
  /* background-color: #00629B; */
  background: linear-gradient(to right, var(--ieee-blue),	#fff);
  left:0;
  height: 5px;
  border-radius: 4px;
}
.title-orange.title-with-hr:before {
  background: linear-gradient(to right, #FFA300,	#fff);
}
.outlined {
  
  -webkit-text-stroke: 1px var(--ieee-blue);
  text-shadow:
  3px 3px 0 var(--ieee-blue),
-1px -1px 0 var(--ieee-blue),  
 1px -1px 0 var(--ieee-blue),
 -1px 1px 0 var(--ieee-blue),
  1px 1px 0 var(--ieee-blue);
}