.preloader2-wrapper {
    width: 100%;
    height: 100vh;
    height: 100dvh;
    margin: 0;
    padding: 0;
    position: absolute;
    z-index:9999999;
    top:0;
    left: 0;
    /* background-color: #FC6042; */
    background-color: var(--ieee-blue);
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;

  }
  
  div.preloader2 {
    width: 8vmax;
    height: 8vmax;
    border-right: 4px solid #fff;
    border-radius: 100%;
    -webkit-animation: spinRight 800ms linear infinite;
            animation: spinRight 800ms linear infinite;
  }
  div.preloader2:before, div.preloader2:after {
    content: "";
    width: 6vmax;
    height: 6vmax;
    display: block;
    position: absolute;
    top: calc(50% - 3vmax);
    left: calc(50% - 3vmax);
    border-left: 5px solid #ffffff;
    border-radius: 100%;
    -webkit-animation: spinLeft 800ms linear infinite;
            animation: spinLeft 800ms linear infinite;
  }
  div.preloader2:after {
    width: 4vmax;
    height: 4vmax;
    top: calc(50% - 2vmax);
    left: calc(50% - 2vmax);
    border: 0;
    border-right: 2px solid #ffffff;
    -webkit-animation: none;
            animation: none;
  }
  
  @-webkit-keyframes spinLeft {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(720deg);
    }
  }
  
  @keyframes spinLeft {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(720deg);
    }
  }
  @-webkit-keyframes spinRight {
    from {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }
  @keyframes spinRight {
    from {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }